.SidebarRow{
    display: flex;
    align-items:center;
    margin:0;
    padding:1px 20px;
}
.sidebarRow_icon{
    color:grey;
     /* margin:0px 10px; */
    /*padding:0; */
    font-size:large !important;
}
.sidebarRow_title{
    flex:1;
    margin-left:20px;
    font-size:15px;
   
}
.SidebarRow:hover{
    background-color:lightgray;
    cursor:pointer;
}
.SidebarRow:hover >.sidebarRow_icon{
    color:red;
    
}
.SidebarRow:hover > .sidebarRow_title{
    font-weight: bold;
}
.SidebarRow.selected{
    background-color: lightgrey;

}
.SidebarRow.selected >.sidebarRow_icon{
        color:red;
}
.SidebarRow.selected>.sidebarRow_title{
    font-weight:bold;
}